import { TILE_SIZE } from './pacman';
export default class Lifebar {
    constructor(sprite) {
        this.sprite = sprite;
        this.lives = 0;
    }
    get name() {
        return 'Lifebar';
    }
    update(gameTime, state) {
        this.lives = state.lives;
    }
    render(gameTime, ctx) {
        const { sprite, lives } = this;
        if (!sprite || !lives) {
            return;
        }
        ctx.save();
        ctx.translate(0, 500);
        for (let i = lives; i > 0; i--) {
            ctx.drawImage(sprite.sprite, TILE_SIZE * 1, 0, TILE_SIZE, TILE_SIZE, TILE_SIZE * i, 0, TILE_SIZE, TILE_SIZE);
        }
        ctx.restore();
    }
}



